export const coolEmojis = ['🖤', '💜', '💙', '💚', '💛', '🧡', '❤️', '🔥', '👌', '🙌', '🤲', '💪', '🎙️', '🎵', '🧙', '✍️', '✌️', '🖖', '🤜', '🤛', '👍', '👊', '✊', '👏', '☢️', '👽', '💉', '🎧', '⚠️', '🔝', '💯', '✨', '💣', '💥', '🥊', '🌶️', '🕶️', '🌡️', '🐲', '☮️', '☯️', '😄', '😃', '😀', '😊', '😉', '😍', '😜', '😝', '😛', '😁', '😂', '😆', '😋', '😎', '😈', '😮', '😬', '😏', '😺', '😸', '😻', '🙈', '🙊']

// Returns a random integer between min (include) and max (include)
export const randomInRange = (min: number, max: number) => Math.floor(Math.random() * (max - min + 1)) + min

export const getRandomFromArr = (arr: unknown[]): unknown => arr[randomInRange(0, arr.length - 1)]

export const getEmojis = (num: number) => {
  const emj: string[] = []
  for (let i = 0; i < num; i++) {
    emj.push(getRandomFromArr(coolEmojis) as string)
  }
  return emj.join(' ')
}

export const toKebabCase = (str: string): string => {
  return str.match(/[A-Z]{2,}(?=[A-Z][a-z]+[0-9]*|\b)|[A-Z]?[a-z]+[0-9]*|[A-Z]|[0-9]+/g)?.map(x => x.toLowerCase()).join('-') || str
}

export const invertHex = (hexString: string): string => {
  const hex = hexString.slice(1)
  const invertedHex = hex ? `#${(Number(`0x1${hex}`) ^ 0xFFFFFF).toString(16).substr(1).toUpperCase()}` : ''
  return invertedHex
}

/**
 * Randomly shuffle an array
 * https://stackoverflow.com/a/2450976/1293256
 * @param  {Array} array The array to shuffle
 * @return {String}      The first item in the shuffled array
 */
export const shuffleArr = <T>(arrayIn: T[]): T[] => {
  const array = arrayIn.slice()
  let currentIndex = array.length
  let temporaryValue
  let randomIndex

  // While there remain elements to shuffle...
  while (currentIndex !== 0) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex)
    currentIndex -= 1

    // And swap it with the current element.
    temporaryValue = array[currentIndex]
    array[currentIndex] = array[randomIndex]
    array[randomIndex] = temporaryValue
  }

  return array
}

// TS version https://stackoverflow.com/a/29416766
export class CircularArray extends Array {
  maxLength: number;

  constructor (maxLength: number) {
    super()
    this.maxLength = maxLength
  }

  public push (...items: unknown[]): number {
    super.push(...items)
    while (this.length > this.maxLength) {
      this.shift()
    }
    return this.length
  }

  public unshift (...items: unknown[]): number {
    super.unshift(...items)
    while (this.length > this.maxLength) {
      this.pop()
    }
    return this.length
  }
}
