/* eslint-disable @typescript-eslint/no-use-before-define */

import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import { auth, profilesCollection } from '@/firebase.config'
import newUserProfile from '@/controllers/NewUserProfile'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/404',
    component: () => import(/* webpackChunkName: "error404" */ '../views/Error404.vue')
  },
  {
    path: '/',
    component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue'),
    beforeEnter: async (to, from, next) => {
      const { currentUser } = auth
      if (currentUser?.uid) {
        await router.replace(`/u/${currentUser?.uid}`)
      }
      next()
    }
  },
  {
    path: '/u/:profileUid',
    props: true,
    component: () => import(/* webpackChunkName: "profile" */ '../views/Profile.vue'),
    beforeEnter: async (to, from, next) => {
      const profileRef = profilesCollection.doc(to.params.profileUid.toString())
      const profileDoc = await profileRef.get()
      const { currentUser } = auth
      const isOwnProfile = currentUser?.uid === to.params.profileUid

      if (!profileDoc?.exists) {
        if (currentUser && isOwnProfile) {
          // User is owner and profile doesn´t exist, create it.
          const newProfile = newUserProfile(currentUser)
          await profileRef.set(newProfile)
        } else {
          // public profile not found, 404...
          await router.replace('/404')
        }
      }
      next()
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
